$clr-primary: hsl(223, 87%, 63%);
$clr-primary-focus: hsl(223, 87%, 65%);
$clr-primary-opac: hsla(223, 87%, 63%, 0.3);
$clr-secondary-1: hsl(223, 100%, 88%);
$clr-secondary-2: hsl(354, 100%, 66%);
$clr-neutral-1: hsl(0, 0%, 59%);
$clr-neutral-2: hsl(209, 33%, 12%);

$mobile: 375px;
$desktop: 1440px;

$font-size: 20px;

$padding-all-1: 2em;

$transition: all 200ms ease;
