footer {
	display: block;

	.img-wrapper {
		width: 100%;

		img {
			width: 100%;

			/* @media only screen and (min-width:500px && min) {
        
      } */
		}
	}

	.footer-wrapper {
		position: absolute;
		bottom: 1em;
		text-align: center;
		right: 0;
		width: 100%;

		.social-icons {
			display: flex;
			justify-content: center;
		}

		.links {
			margin-left: 0.5em;
			margin-right: 0.5em;
			color: $clr-primary;
			border: 1px solid $clr-secondary-1;
			border-radius: 50%;
			padding: 0.5em;
			font-size: 0.8em;

			display: flex;
			width: auto;
			align-items: center;
			justify-content: center;
			transition: $transition;

			&:hover {
				background-color: $clr-primary;
				color: white;
			}
		}

		p {
			font-size: 0.7em;
			font-weight: 300;
			color: $clr-neutral-1;
		}
	}
}
