.container {
	width: 100%;
	padding-left: $padding-all-1;
	padding-right: $padding-all-1;
	height: 100%;

	/* display: flex;
	justify-content: center;
	align-items: center; */

	.container-wrapper {
		width: 100%;
		max-width: 600px;
		margin: 0 auto;
		transition: $transition;

		@media only screen and (min-width: 620px) {
			max-width: 450px;
		}
		@media only screen and (min-width: $desktop) {
			max-width: 600px;
		}
	}
}

.error {
	font-size: 0.6em;
	color: $clr-secondary-2;
	font-style: italic;
	margin-top: 0;
}

a:focus {
	outline: 2px dashed $clr-primary-opac;
	outline-offset: 3px;
}
a:hover {
	color: $clr-neutral-1;
}
