@import url(https://fonts.googleapis.com/css2?family=Libre+Franklin:wght@300;600;700&display=swap);
.container{width:100%;padding-left:2em;padding-right:2em;height:100%}.container .container-wrapper{width:100%;max-width:600px;margin:0 auto;transition:all 200ms ease}@media only screen and (min-width: 620px){.container .container-wrapper{max-width:450px}}@media only screen and (min-width: 1440px){.container .container-wrapper{max-width:600px}}.error{font-size:0.6em;color:#ff5263;font-style:italic;margin-top:0}a:focus{outline:2px dashed rgba(79,125,243,0.3);outline-offset:3px}a:hover{color:#969696}header{width:100%;text-align:center;padding-top:2em}header img{width:70px}main{text-align:center}main h1{font-weight:300;color:#969696;font-size:1.2em;margin-bottom:1em;margin-top:1.5em}main h1 strong{font-weight:700;color:#151f29}main>p{margin-bottom:2.3em;font-size:0.8em}main form{margin-bottom:2em}@media only screen and (min-width: 1440px){main form{display:grid;grid-template-columns:2fr 1fr;-webkit-align-items:center;align-items:center}}main form div{position:relative}@media only screen and (min-width: 1440px){main form div p{position:absolute;bottom:-40px;left:0;-webkit-transform:translateX(20%);transform:translateX(20%)}}main form input,main form button{padding:1em 2em;font-size:0.7em;border-radius:2em;transition:all 200ms ease}main form input{border:2px solid #c2d3ff;outline:none;width:100%}main form input:focus{outline:2px dashed rgba(79,125,243,0.3);outline-offset:3px}main form input:invalid{border:2px solid #ff5263}main form input::-webkit-input-placeholder{color:#c2d3ff}main form input:-ms-input-placeholder{color:#c2d3ff}main form input::placeholder{color:#c2d3ff}main form .input-error{border:2px solid #ff5263}main form button{width:100%;text-transform:capitalize;background-color:#4f7df3;color:white;border:none;box-shadow:0px 5px 15px 2px rgba(79,125,243,0.3);cursor:pointer;font-weight:600;margin-top:0.8em}@media only screen and (min-width: 1440px){main form button{margin-top:0;margin-left:0.5em}}main form button:focus{outline:2px dashed rgba(79,125,243,0.3);outline-offset:3px}main form button:hover{background-color:#5884f3}footer{display:block}footer .img-wrapper{width:100%}footer .img-wrapper img{width:100%}footer .footer-wrapper{position:absolute;bottom:1em;text-align:center;right:0;width:100%}footer .footer-wrapper .social-icons{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center}footer .footer-wrapper .links{margin-left:0.5em;margin-right:0.5em;color:#4f7df3;border:1px solid #c2d3ff;border-radius:50%;padding:0.5em;font-size:0.8em;display:-webkit-flex;display:flex;width:auto;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;transition:all 200ms ease}footer .footer-wrapper .links:hover{background-color:#4f7df3;color:white}footer .footer-wrapper p{font-size:0.7em;font-weight:300;color:#969696}*{box-sizing:border-box}html{font-family:'Libre Franklin', sans-serif;font-size:20px}

