main {
	text-align: center;

	h1 {
		font-weight: 300;
		color: $clr-neutral-1;
		font-size: 1.2em;
		margin-bottom: 1em;
		margin-top: 1.5em;

		strong {
			font-weight: 700;
			color: $clr-neutral-2;
		}
	}

	& > p {
		margin-bottom: 2.3em;
		font-size: 0.8em;
	}

	form {
		margin-bottom: 2em;

		@media only screen and (min-width: $desktop) {
			display: grid;
			grid-template-columns: 2fr 1fr;
			align-items: center;
		}

		div {
			position: relative;

			p {
				@media only screen and (min-width: $desktop) {
					position: absolute;
					bottom: -40px;
					left: 0;
					transform: translateX(20%);
				}
			}
		}

		input,
		button {
			padding: 1em 2em;
			font-size: 0.7em;
			border-radius: 2em;
			transition: $transition;
		}

		input {
			border: 2px solid $clr-secondary-1;
			outline: none;
			width: 100%;

			&:focus {
				outline: 2px dashed $clr-primary-opac;
				outline-offset: 3px;
			}

			&:invalid {
				border: 2px solid $clr-secondary-2;
			}

			&::-webkit-input-placeholder {
				color: $clr-secondary-1;
			}

			&::-moz-placeholder {
				color: $clr-secondary-1;
			}

			&:-ms-input-placeholder {
				color: $clr-secondary-1;
			}

			&::-ms-input-placeholder {
				color: $clr-secondary-1;
			}

			&::placeholder {
				color: $clr-secondary-1;
			}
		}

		.input-error {
			border: 2px solid $clr-secondary-2;
		}

		button {
			width: 100%;
			text-transform: capitalize;
			background-color: $clr-primary;
			color: white;
			border: none;
			box-shadow: 0px 5px 15px 2px $clr-primary-opac;
			cursor: pointer;
			font-weight: 600;
			margin-top: 0.8em;

			@media only screen and (min-width: $desktop) {
				margin-top: 0;
				margin-left: 0.5em;
			}

			&:focus {
				outline: 2px dashed $clr-primary-opac;
				outline-offset: 3px;
			}

			&:hover {
				background-color: $clr-primary-focus;
			}
		}
	}
}
